.prod {
	background-position-y: 88%;
	background-repeat: no-repeat;
    background-size: cover;
	.product {
		background: url(../img/pacifier.png) right 65% no-repeat,
				url(../img/bottle.png) left 55% no-repeat;
	    background-size: 11%, 11%;
	    box-shadow: inset 0px 15px 30px -18px rgba(50, 50, 50, 0.4);

		.product__wrap {
			max-width: 1330px;
			width: 100%;
			min-height: 1250px;
			flex-direction: column;
	        justify-content: flex-start;
		    background-image: url(../img/product-bg.png);
		    background-size: 100% 100%;
		    background-repeat: no-repeat;

		    .product__breadcrumps {
		    	margin: 20px 0 30px 145px;
	   	        align-self: flex-start;

	   	        @include breakpoint(max-width 992px) {
	   	        	margin: 10px 0 15px 70px;
	   	        }

	   	        @include breakpoint(max-width 768px) {
	   	        	margin: 0;
	   	        	margin-left: 10px;
	   	        }

	   	        @include breakpoint(max-width 576px) {
	   	        	margin: 0;
	   	        }

		    	ul {
		    		display: flex;
		    		list-style: none;
		    		color: #203350;
					
					@include breakpoint(max-width 768px) {
						flex-wrap: wrap;
					}

		    		li a {
		    			font-weight: 500;
		    			color: #203350;
		    			&.active {
		    				font-weight: 800;
		    				color: #224c92;
		    			}
		    		}
		    	}
		    }

		    .product__content-container {
		    	max-width: 960px;
		    	width: 89.6%;
		    	.product__content {
		    		display: flex;
		    		justify-content: space-between;

					@include breakpoint(max-width 992px) {
						justify-content: space-around;
						flex-wrap: wrap;
					}

		    		.product__content-gallery {
		    			max-width: 430px;
		    			width: 100%;

		    			img {
		    				max-width: 100%;
		    			}
		    		}

		    		.product__content-img {
		    			width: 100%;

		    			img {
				            width: auto;
				            height: 100%;
				            max-height: 380px;
				        }
		    		}

		    		.slider-nav {
		    			max-width: 380px;
		    			width: 100%;
		    			margin: 20px auto;

		    			@include breakpoint(max-width 576px) {
		    				width: 84%;
		    			}

						.slick-slide {
							display: flex;
							align-items: center;
							height: 90px;
							width: 90px;
							margin: 3px;
							border: 1px solid #eef7ff;
							background-color: white;
							cursor: pointer;

							@include breakpoint(max-width 576px) {
								height: 118px;
								width: 118px;
							}

							&:hover {
								border-color: #224c92;
							}
						}
		    		}

		    		.product__content-price {
		    			max-width: 450px;
		    			width: 95%;

		    			.title h3,
		    			.cost {
		    				margin-top: 0;
		    				font-family: 'Lato-Black', sans-serif;
		    				font-size: 22px;
		    				color: #1c4a93;
		    			}

		    			.cost {
		    				.old-cost {
		    					margin-right: 10px;
		    					font-family: 'Lato-Regular', sans-serif;
		    					text-decoration: line-through;
		    					color: black;
		    				}

		    				.economy {
		    					margin-top: 5px;
		    					margin-bottom: 30px;
		    					font-size: 12px;
		    					color: black;
		    				}

		    				p:first-child {
								margin-bottom: 0;
		    				}
		    			}

		    			.price-describe {
		    				min-height: 250px;
							padding: 10px 25px;
							font-size: 14px;
							line-height: 1.4;
		    				background-image: url(../img/product-desc.png);
		    				background-repeat: no-repeat;
		    				background-size: 100% 100%;
		    				box-sizing: border-box;

		    				p {
		    					margin: 8px 0;
		    				}

		    				.blue {
		    					margin: 0;
		    					font-family: 'Lato-Bold', sans-serif;
		    					font-size: 15px;
		    					color: #224c92;
		    				}

		    				.calory {
		    					margin: 0;
		    					display: flex;
		    					flex-wrap: wrap;
		    					font-size: 15px;
		    					span {
		    						width: 35%;
		    					}
		    				}
		    			}

		    			.buy {
		    				display: flex;
		    				justify-content: space-between;
		    				button {
		    					cursor: pointer;
		    				}
		    			}

		    		.buy-one-click {
		    			position: relative;
		    			padding: 13px 25px;
						border: 1px solid #1c549f;
						border-radius: 23px;
						background-color: #1c549f;
						color: white;
						transition: all 0.2s;
						&:before {
							content: "";
							position: absolute;
							top: 3px;
							right: 3px;
							bottom: 3px;
							left: 3px;
							border: 1px dashed #fff;
							border-radius: 21px;
						}

						&:hover {
							transition: all 0.2s;
							background-color: #053e73;
						}

	    			}

	    			.add-to-cart {
	    				padding: 13px 25px;
	    				border: 1px solid #1c549f;
	    				background-color: transparent;
	    				color: #1c549f;
	    				border-radius: 23px;
	    				transition: all 0.2s;

	    				&:hover {
	    					transition: all 0.2s;
	    					background-color: #1c549f;
	    					color: white;
	    				}
	    			}
		    	}

		    		.amount {
		    			margin: 10px 0;
		    			display: flex;
		    			align-items: center;
		    		}
		    	}
		    }
		}
	}
}


.product__describe {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;

	@include breakpoint(max-width 992px) {
		justify-content: space-around;
	}

	.product__describe-text {
		max-width: 420px;

		@include breakpoint(max-width 992px) {
			max-width: 95%;
		}

		h3 {
			margin-bottom: 10px;
			font-family: 'Lato-Black', sans-serif;
			font-weight: normal;
			font-size: 20px;
			color: #1653a0;
			&:first-child {
				text-transform: uppercase;
			}
		}

		p, ul {
			margin: 10px 0;
		}

		ul {
			padding: 0;
			list-style: none;
		}

		li {
			position: relative;
			margin: 7px 0;
			padding-left: 20px;

			&:before {
				content: '';
				position: absolute;
				top: 7px;
				left: 0;
				width: 8px;
				height: 8px;
				background-color: #1653a0;
				border-radius: 50%;
			}
		}
	}

	.product__describe-feedback {
		max-width: 450px;

		@include breakpoint(max-width 992px) {
			max-width: 95%;
		}

		.delivery {
			h4 {
				color: #ff833d;
				font-family: 'Lato-BoldI', sans-serif;
				font-weight: normal;
				font-style: normal;
				font-size: 18px;

				span {
					margin-top: 10px;
					font-family: 'Lato-Black', sans-serif;
					font-size: 22px;
					font-style: normal;
					color: #1653a0;
				}
			}
		}

		.feedbacks {

			h3 {
				font-family: 'Lato-Black', sans-serif;
				font-weight: normal;
				font-size: 20px;
				color: #1653a0;
				text-transform: uppercase;
			}

			.feedback {
				position: relative;
				display: flex;
				align-items: flex-start;
				min-height: 240px;
				padding: 20px;
				border-radius: 0 0 200px;
				border: 1px solid #ffffff;
				background-color: #eef7ff;
				box-sizing: border-box;

				@include breakpoint(max-width 576px) {
					justify-content: space-around;
					flex-wrap: wrap;
					padding: 10px;
				}

				@include breakpoint(max-width 400px) {
					min-height: 380px;
					border-radius: 0 0 100px;
				}

				.feedback-img {
					width: 100px;
					height: 110px;

					img {
						width: 100%;
						height: auto;
					}
				}

				.feedback-text {
					min-width: 213px;
					margin-left: 30px;

					@include breakpoint(max-width 992px) {
						width: 75%;
					}

					@include breakpoint(max-width 768px) {
						width: 60%;
					}

					@include breakpoint(max-width 576px) {
						margin: 0 auto;
					}

					h4 {
						margin: 0;
						font-family: 'Corinthia', sans-serif;
						font-weight: normal;
						font-size: 40px;
					}

					p {
						margin-bottom: 0;
						font-family: 'Myriad', sans-serif;
						font-size: 15px;

						@include breakpoint(max-width 992px) {
							width: 100%;
						}
					}
				}
				
				.feedback-like {
					position: absolute;
					top: 30%;
					right: -25px;
				}
			}
		}

		.feedback-form {
				#feedback-form {
					display: flex;
					justify-content: space-between;
					flex-wrap: wrap;

					legend {
						width: 100%;
						margin: 40px 0 15px;
						font-family: "Lato-Black", sans-serif;
						font-weight: normal;
						font-size: 20px;
						color: #1653a0;
						text-transform: uppercase;
					}

					input {
						width: 220px;
						margin-bottom: 20px;
						padding: 8px 15px;
						font-size: 13px;
						letter-spacing: 0.33px;
						color: #1c549f;
						border: 1px solid #eef7ff;
						border-radius: 18px;
					}

					textarea {
						width: 100%;
						height: 90px;
						margin-bottom: 15px;
						padding: 8px 15px;
						font-size: 13px;
						color: #1c549f;
						border-radius: 5px;
						border: 1px solid #eef7ff;
						resize: none;
					}

					label {
						display: flex;
						align-items: center;
						width: 175px;
						font-size: 13px;

						input {
							width: auto;
							margin: 0;
							margin-right: 10px;
						}
					}

					button {
						display: block;
						width: 190px;
						margin: 40px auto;
						padding: 15px 0;
						position: relative;
						border: none;
						border-radius: 23px;
						border: 1px solid #1c549f;
						background-color: #1c549f;

						color: white;
						cursor: pointer;
						transition: all 0.2s;
						&:before {
							content: "";
							position: absolute;
							top: 3px;
							right: 3px;
							bottom: 3px;
							left: 3px;
							border: 1px dashed #fff;
							border-radius: 32px;
						}

						&:hover {
							transition: all 0.2s;
							background-color: #053e73;
						}
					}
				}
			}
	}
}

.prod .popular {
	left: 0;
	display: flex;
	flex-wrap: wrap;
	margin: 30px auto 0;
	padding-bottom: 10px;
	background-color: white;
}

.another,
.similar {
	max-width: 980px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	margin: 0 auto;

	@include breakpoint(max-width 992px) {
		justify-content: space-around;
	}

	h2 {
		width: 100%;
	}
}

.prod .product .popular .another h2 {
	margin-bottom: 50px;
	margin-left: 0;
	font-family: 'Lato-Bold', sans-serif;
	font-weight: normal;
	color: #ff833d;
	text-transform: none;
}

.another-item,
.similar__product {
	padding: 20px 10px 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	border-radius: 5px;
	transition: box-shadow .5s;
	&:hover {
		transition: box-shadow .5s;
		box-shadow: 10px 4px 29px rgba(0,0,0,.22);
	}
}

.another-item {
	max-width: 190px;

	.another-img {
		max-width: 130px;
		max-height: 130px;
		border: 1px solid #eef7ff;

		img {
			max-width: 100%;
			max-height: 100%;
			vertical-align: middle;
		}
	}
}

.similar__product {
	width: 300px;
	padding: 30px 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;

	&-price {
		font-family: Appetite,sans-serif;
		font-size: 29px;
		color: #1653a0;
	}

	&-buy {
		margin: 20px 0 25px;
		a {
			padding: 15px 28px 16px 27px;
			background: linear-gradient(270deg,#1655a2,#22316f);
			color: #fff;
			border-radius: 22px;

			&:hover, &:active, &:focus {
				background: #1655a2;
			}
		}
	}

	&-to-cart a {
		font-size: 14px;
		color: #053e73;
		text-decoration: underline;

		&:hover, &:active, &:focus {
			opacity: 0.8s;
			text-decoration: none;
		}
	}
}

.another-title,
.similar__product-title {
	h4 a {
		font-family: 'Lato-Black', sans-serif;
		color: #1653a0;
		line-height: 21px;
		text-transform: uppercase;
	}
}



.prod .product .popular .similar h2 {
	margin-left: 0;
	margin-bottom: 25px;
}

